import dynamic from 'next/dynamic';
import { memo, useCallback, useEffect, useRef, useState } from 'react';
import { Type_RefFuncKrpano } from '@components/Krpano/types';
import { useSelector } from 'react-redux';
import {
  selectDetailProject,
  selectSceneSelected,
  selectThemeConfigGlobal,
} from '@containers/Home/selectors';
import { I_Scene } from '@containers/Home/types';
import { ControlbarWrapper } from './styled';
import ListScene, { Type_RefFuncSlide } from './ListScene';

const ControlbarAction = dynamic(() => import('./Controlbar.Action'));

interface Props {
  krpanoRef: Type_RefFuncKrpano | null;
  onChangeScene: (scene: I_Scene) => void;
}

export const Controlbar = memo(({ krpanoRef, onChangeScene }: Props) => {
  const listSceneRef = useRef<Type_RefFuncSlide>(null!);
  const themeConfig = useSelector(selectThemeConfigGlobal);
  const sceneSelected = useSelector(selectSceneSelected);
  const detailProject = useSelector(selectDetailProject);

  const [isShowListScene, setIsShowListScene] = useState(false);
  const [isShowControlbar, setIsShowControlbar] = useState(true);

  const onToggleControlbar = useCallback(
    () => setIsShowControlbar(prev => !prev),
    [],
  );

  const onToggleListScene = useCallback(
    () => setIsShowListScene(prev => !prev),
    [],
  );

  const onPrevScene = useCallback(
    () => listSceneRef.current?.onPrevScene(),
    [],
  );
  const onNextScene = useCallback(
    () => listSceneRef.current?.onNextScene(),
    [],
  );

  useEffect(() => {
    if (themeConfig?.selected === 'basekrpano')
      setIsShowListScene(!!themeConfig?.config?.autoOpenSceneList);
  }, [themeConfig]);

  return (
    <ControlbarWrapper
      id="themeControlbar"
      className={`--controlbar-${isShowControlbar ? 'on' : 'off'}`}
    >
      <div className="--title hide-scrollbar">
        <h1>{detailProject?.title || 'N/A'}</h1>
        <span> &nbsp;Â·&nbsp; </span>
        <span>{sceneSelected?.title || 'N/A'}</span>
      </div>

      <div className="--mainPanel">
        <ListScene
          ref={listSceneRef}
          onChangeScene={onChangeScene}
          isShowListScene={isShowListScene}
        />
        <ControlbarAction
          krpanoRef={krpanoRef}
          onPrevScene={onPrevScene}
          onNextScene={onNextScene}
          isShowListScene={isShowListScene}
          isShowControlbar={isShowControlbar}
          onToggleListScene={onToggleListScene}
          onToggleControlbar={onToggleControlbar}
        />
      </div>
    </ControlbarWrapper>
  );
});

export default Controlbar;
